import { Component, OnInit, EventEmitter, QueryList, ElementRef, ViewChildren, AfterViewInit } from '@angular/core';
import * as AmazonCognitoIdentity from 'amazon-cognito-identity-js';
import { Router } from '@angular/router';
import * as AWS from 'aws-sdk';
import { IdentityPool, Region, PoolData, BucketName, TableName } from '../lib/aws-config';

type User = {
  token: string,
  userPoolId: string,
  region: string
};

type Instance = {
  BoundingBox: {
    Width: number,
    Height: number,
    Left: number,
    Top: number
  },
  Confidence: number
};

type Label = {
  Name: string,
  Confidence: number,
  Instances: Instance[]
};

const CavnasIds = ['myCanvas1', 'myCanvas2'];

@Component({
  selector: 'app-image-processor',
  templateUrl: './image-processor.component.html',
  styleUrls: ['./image-processor.component.scss']
})

export class ImageProcessorComponent implements OnInit, AfterViewInit {
  @ViewChildren('canvas') canvas: QueryList<ElementRef>;

  private informationLoaded: EventEmitter<Instance[]>;
  private _selectedFile: File = null;
  private _selectedFiles: File[] = [null, null];
  private _preProccessedImageSrc = null;
  private _preProccessedImages: any[] = [null, null, null];
  private token;
  private user: User;
  private rekognitionDataObjects: any[] = [null, null, null];
  private _rekognitionData: AWS.Rekognition.DetectLabelsResponse = null;
  public s3Image = null;
  public garageNumber = 0;

  constructor(private router: Router) { 
    // console.log('Component Constructed');

    this.user = {
      token : localStorage.getItem('aws_id_token'),
      userPoolId : PoolData.UserPoolId,
      region: Region
    };
    this.token = localStorage.getItem('aws_id_token');

    this.informationLoaded = new EventEmitter<Instance[]>();

    this.informationLoaded.subscribe((event: Instance[]) => {
      if (this.rekognitionDataObjects[this.garageNumber] === null) {
        this.rekognitionDataObjects[this.garageNumber] = event;
      }
      this.drawPreProcessedImage(this._preProccessedImages[this.garageNumber], CavnasIds[this.garageNumber]);
      // this.drawBoxes(event, CavnasIds[this.garageNumber]);
    });



    // console.log(this.token);
  }

  ngAfterViewInit() {
    console.log(this.canvas.toArray());
    this.canvas.changes.subscribe(val => {
      // console.log(this.canvas.toArray());
      if (this._preProccessedImages[this.garageNumber] !== null){
        this.drawPreProcessedImage(this._preProccessedImages[this.garageNumber], CavnasIds[this.garageNumber]);
      }
    });

  }

  ngOnInit() {
    if (!this.token) {
      this.router.navigate(['/login']);
    }
    else {
      let login = `cognito-idp.${this.user.region}.amazonaws.com/${this.user.userPoolId}`;
      // console.log(login);
      AWS.config.credentials = new AWS.CognitoIdentityCredentials({
        IdentityPoolId: IdentityPool.Id,
        Logins: {
          [login] : this.user.token
        }
      });
      AWS.config.region = Region;
    }

    // let canvas = <HTMLCanvasElement>document.getElementById('myCanvas');
    // let ctx = canvas.getContext('2d');
    // ctx.rect(200, 200, 50, 50);
  }
  

  public onFileSelected(event) {
    this._selectedFile = <File>event.target.files[0];
    this._selectedFiles[this.garageNumber] = <File>event.target.files[0];
    this.rekognitionDataObjects[this.garageNumber] = null;
    // console.log(this._selectedFile);
    let reader = new FileReader();
    reader.onloadend = () => {
        //  this._preProccessedImageSrc = reader.result;
         this._preProccessedImages[this.garageNumber] = reader.result;
         this.drawPreProcessedImage(reader.result, CavnasIds[this.garageNumber]);
        //  console.log(this._preProccessedImageSrc);
    };
    reader.readAsDataURL(this._selectedFiles[this.garageNumber]);
  }

  public sendToS3() {
    let S3 = new AWS.S3({apiVersion: '2006-03-01', region: Region});

    // let key = `test-images/test123.png`;
    let key = 'test-images/Screen Shot 2019-01-07 at 2.35.11 PM.png';
    // let params = {
    //   Bucket: BucketName, 
    //   Key: key
    //  };
    let objKey = 'test-images/test_file.' + Date.now().toString();  // + this._selectedFile.name;
    // console.log(objKey);

    //  let objKey = 'test-images/' + this._selectedFile.name;
     let uploadParams: AWS.S3.PutObjectRequest = {
         Key: objKey,
         ContentType: this._selectedFiles[this.garageNumber].type,
         Body: this._selectedFiles[this.garageNumber],
         Bucket: BucketName
     };

     S3.putObject(uploadParams, (err, data) => {
      if (err) {
        console.log('error here: ', err);
          // results.innerHTML = 'ERROR: ' + err;
      } else {
        //set timeout to grab dynamoinfo here
        // console.log('about to check dynamo');
        let t = null;
        let getDynamoInfo = (tableName: string, dynamoKey: string, emitter: EventEmitter<Instance[]>) => {
          let db = new AWS.DynamoDB();
          let dbParams: AWS.DynamoDB.GetItemInput = {
            Key: {key: {'S': dynamoKey}},
            TableName: tableName,
            AttributesToGet: ['payload'],
            ConsistentRead: true
          };
          // console.log(dbParams);
          db.getItem(dbParams, function(db_err, db_data){
            // console.log('db_err: ', db_err);
            if (db_err || Object.keys(db_data).length === 0) {
              // console.log('about to reset timer...');
              console.log(db_err);
              t = setTimeout(function () {getDynamoInfo(TableName, dynamoKey, emitter); }, 200);
            }
            else {
              console.log('about to clear timer...');
              console.log('db_data: ', db_data);
              this._rekognitionData = JSON.parse(<string>db_data.Item['payload'].S);
              
              let cars_index = this._rekognitionData.Labels.findIndex((label: Label) => label.Name === 'Car');
              console.log(this._rekognitionData);
              emitter.emit(this._rekognitionData.Labels[cars_index].Instances);
              // this.drawBoxes(this._rekognitionData.Labels[cars_index]); //can't access inside function

              clearTimeout(t);
            }
          });
        };
        getDynamoInfo(TableName, objKey, this.informationLoaded);
        // console.log('data: ', data);
      }
    });
  }

  public drawBoxes(carLabels: Instance[], canvasId){
    console.log('car labels: ', carLabels);
    let canvas = <HTMLCanvasElement>document.getElementById(canvasId);
    let ctx = canvas.getContext('2d');
    // ctx.beginPath();
    carLabels.forEach((label) => {
      ctx.rect(
        canvas.width * label.BoundingBox.Left,
        canvas.height * label.BoundingBox.Top,
        canvas.width * label.BoundingBox.Width,
        canvas.height * label.BoundingBox.Height
      );
      ctx.strokeStyle = 'blue';
      ctx.stroke();
      console.log(label.BoundingBox);
    });
  }

  get selectedFile() {
    return this._selectedFiles[this.garageNumber];
  }

  get preProccessedImageSrc() {
    return this._preProccessedImages[this.garageNumber];
  }

  public nextGarage(canvasId: string) {
    this.garageNumber++;
    // this.drawPreProcessedImage(this._preProccessedImages[this.garageNumber], canvasId);
    // if (this.rekognitionDataObjects[this.garageNumber] !== null) {
    //   this.drawBoxes(this.rekognitionDataObjects[this.garageNumber], CavnasIds[this.garageNumber]);
    // }
    
  }

  public previousGarage(canvasId: string){
    this.garageNumber--;
    console.log('here...');
    // this.drawPreProcessedImage(this._preProccessedImages[this.garageNumber], canvasId);
    // console.log(canvasId);
    // if (this.rekognitionDataObjects[this.garageNumber] !== null) {
    //   console.log('masashi here');
    //   this.drawBoxes(this.rekognitionDataObjects[this.garageNumber], CavnasIds[this.garageNumber]);
    // }
  }

  private drawPreProcessedImage(imgSrc, canvasId){
    let img = new Image();
    img.onload = () => {
      let canvas = <HTMLCanvasElement>document.getElementById(canvasId);
      console.log(img);
      let ctx = canvas.getContext('2d');
      console.log('about to clear');
      ctx.clearRect(0, 0, canvas.width, canvas.height);

      console.log('cleared');
      // ctx.
      // ctx.clearRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(img, 0, 0, img.width, img.height, 
        0, 0, canvas.width, canvas.height);
      ctx.beginPath();
      if (this.rekognitionDataObjects[this.garageNumber] !== null) {
        this.drawBoxes(this.rekognitionDataObjects[this.garageNumber], CavnasIds[this.garageNumber]);
      }

    };
    img.src = imgSrc;
    
    // let img = document.createElement('img');

  }

  public getNumberOfCars(index){
    return (this.rekognitionDataObjects[index] !== null ? this.rekognitionDataObjects[index].length.toString() + ' cars' 
      : 'No image processed');
  }

}
