import { Injectable } from '@angular/core';
import { PoolData } from './user-config';
import { HttpModule } from '@angular/http';
import * as AmazonCognitoIdentity from 'amazon-cognito-identity-js';
import { Router } from '@angular/router';

type PoolData = {
    UserPoolId: string,
    Region: string,
    ClientId: string
};

@Injectable()
export class UserService {
    private poolData: PoolData = PoolData;
    constructor(public http: HttpModule, private router: Router) {
        
    }

    public login(username: string, password: string) {
        return new Promise((resolve, reject) => {
            console.log('username: ', username);
            console.log('password: ', password);
            console.log('pool data: ', this.poolData);
            let authenticationData = {
                Username : username,
                Password : password,
            };
            let authenticationDetails = new AmazonCognitoIdentity.AuthenticationDetails(authenticationData);
    
            let userPool = new AmazonCognitoIdentity.CognitoUserPool(this.poolData);
            let userData = {
                Username : username,
                Pool : userPool
            };
            
    
            let cognitoUser = new AmazonCognitoIdentity.CognitoUser(userData);
            cognitoUser.authenticateUser(authenticationDetails, {
                onSuccess: (result) => {
                    let accessToken = result.getIdToken().getJwtToken();
                    console.log('result: ', result);
                    /* Use the idToken for Logins Map when Federating User Pools with identity pools or when 
                    passing through an Authorization Header to an API Gateway Authorizer*/
                    
                    let idToken = result.getIdToken().getJwtToken();
                    localStorage.setItem('aws_id_token', idToken);
                    // localStorage.setItem('aws_user_pool_id');
                    // localStorage.setItem('', re)
                    // console.log('idToken: ', idToken);
                    return resolve(true);
                    // this.router.navigate(['/demo']);
                },
    
                onFailure: function (err) {
                    return reject(err);
                },
                newPasswordRequired: function (userAttributes, requiredAttributes) {
                    console.log('User needs new password');
                    console.log(userAttributes, requiredAttributes);
                    delete userAttributes.email_verified;
                    cognitoUser.completeNewPasswordChallenge(password, userAttributes, this);
                }
            });
        });
    }
}
